//Tools


module.exports = {
    // XSS 转义函数
    encode: function (html) {
        var rstr = html.replace(/&/gim, '&amp;')
            .replace(/</gim, '&lt;')
            .replace(/>/gim, '&gt;')
            .replace(/\"/gim, '&quot;')
            .replace(/\'/gim, '&apos;')
            .replace(/ /gim, '&nbsp;');
        return rstr;
    },
    encodejs: function (text) {
        var rstr = text
            .replace(/\"/gim, '\\"')
            .replace(/\'/gim, "\\'");
        return rstr;
    },
    decode: function (text) {
        var str = text
            .replace(/&lt;/gim, '<')
            .replace(/&gt;/gim, '>')
            .replace(/&quot;/gim, '"')
            .replace(/&apos;/gim, "'")
            .replace(/&nbsp;/gim, ' ')
            .replace(/<script>/gim, "")
            .replace(/&amp;/gim, '&');
        return str;
    }
}